<template>
  <div class="grid grid-cols-10 lg:gap-10 overview">
    <div class="col-span-10 lg:col-span-6">
      <div class="mb-6">
        <p>
          <b>{{ $t("userPage.activities") }}</b>
        </p>
      </div>
      <Activitie />
      <Activitie />
    </div>
    <div class="col-span-10 lg:col-span-4">
      <div class="mb-6" v-if="about != null && about != ''">
        <p>
          <b>{{ $t("userPage.about") }}</b>
        </p>
      </div>
      <div
        class="about-block bg-black mb-8"
        v-if="about != null && about != ''"
      >
        <p class="color-gray">
          {{ about }}
        </p>
      </div>
      <div class="mb-6">
        <p>
          <b>{{ $t("userPage.game_account") }}</b>
        </p>
      </div>
      <GameAccount
        v-for="(item, index) in gameAccount"
        :key="index"
        :data="item"
      />
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import Activitie from "@/components/baseUI/Activitie.vue";
import GameAccount from "@/components/baseUI/GameAccount.vue";
import axios from "axios";
import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      about: "",
    };
  },
  props: {
    gameAccount: {
      type: Array,
    },
  },
  components: {
    Activitie,
    GameAccount,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getInfo() {
      var response = await axios.get(this.apiUrl + "/profile/info/", {
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.about = response.data.about;
      }
    },
  },
  async created() {
    this.getInfo();
  },
});
</script>
<style lang="scss" scoped>
.overview {
  .about-block {
    padding: 1.5rem;
    border-radius: 16px;
  }
}
</style>
